import React from 'react';

import {Button, Glyphicon} from 'react-bootstrap'

export default class Resume extends React.Component {

  render() {
    return (
      <div>
        <Button bsSize="large">
            <Glyphicon glyph="glyphicon glyphicon-download-alt" /> Download PDF Version
        </Button>
      </div>
    );
  }
}


